<template>
  <el-row class="vip-video-player">
    <el-row style="background-image: linear-gradient(120deg, #e0c3fc 0%, #8ec5fc 100%);">
      <h3 class="text-center">复制视频播放地址, 粘贴到输入框中, 点击播放即可。</h3>
      <el-row type="flex" justify="center" class="plr-20">
        <el-input size="medium" placeholder="请输入视频播放地址" v-model="videoSrc" style="width: 800px;">
          <el-button slot="append" size="medium" icon="el-icon-video-play" @click="playVideo">播放</el-button>
        </el-input>
      </el-row>
      <el-row class="text-center mt-20">
        <el-row style="color: #909399;">无法播放？实施切换线路</el-row>
        <el-radio-group v-model="selectLine" @change="playVideo">
          <el-radio-button class="m-10" v-for="(item,index) in api" :key="index" :label="item.value">{{item.label}}
          </el-radio-button>
        </el-radio-group>
      </el-row>
    </el-row>
    <el-row v-if="videoSrc">
      <iframe id="$iframe" src="" frameborder="0" width="100%" height="800px" :allowfullscreen="true"></iframe>
    </el-row>
  </el-row>
</template>
<script>
import { getVideoList } from '@/api/video-parse'

export default {
  name: 'vip-video-play',
  data () {
    return {
      selectLine: 'http://api.ledboke.com/?url=',
      videoSrc: '',
      api: [
        { label: '线路一', value: 'http://api.ledboke.com/?url=' },
        { label: '线路二', value: 'http://ckplay.online/?url=' },
        { label: '线路三', value: 'http://mimijiexi.top/?url=' },
        { label: '线路四', value: 'https://api.bbbbbb.me/jx/?url=' },
        { label: '线路五', value: 'http://api.baiyug.vip/index.php?url=' },
        { label: '线路六', value: 'http://607p.com/?url=' },
      ]
    }
  },
  methods: {
    playVideo () {
      if (this.videoSrc) {
        let $iframe = document.getElementById('$iframe')
        $iframe.src = this.selectLine + this.videoSrc
      } else {
        this.$message.warning('请粘贴要观看的资源地址')
      }

    },
  }
}
</script>
<style lang="scss">
.vip-video-player {

}
</style>
